<template>
  <div class="base-info-container">
    <div class="content ball__shadow" v-html="textShow">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text: "姓名：程天乐 性别：男 生日：1997年12月30日",
      textShow: "",
      textLen: 0,
      line__show: true,
      timer: null,
    };
  },
  created() {
    this.timer = setInterval(() => {
      this.textLen += 1;
      this.textShow = this.text.substring(0, this.textLen);
      if (this.textLen == this.text.length) {
        clearInterval(this.timer);
      }
    }, 100);
  },
};
</script>

<style scoped>
.base-info-container {
  width: 100%;
  height: 100%;
  position: relative;
  background: url("~@/assets/aboutbg.jpeg");
  background-position: center center;
  background-size: cover;
  overflow: hidden;
}
.content {
  width: 60%;
  height: 80%;
  margin: 5% auto;
  padding: 20px;
  box-sizing: border-box;
  font-size: 20px;
  color: #fff;
  vertical-align: bottom;
}
.ball__shadow {
  box-shadow: 1px 1px 10px 0 #fff, inset 1px 1px 10px 0 #fff,
    inset 1px 1px 10px 0 #fff, inset -1px -1px 20px 0 #fff,
    inset -1px -1px 20px 0 #fff;
}
.line {
  display: none;
  width: 1px;
  background: #fff;
  font-size: inherit;
  height: 1em;
  vertical-align: bottom;
}
.line__show {
  display: inline-block;
}
</style>